* {
  margin: 0;
  padding: 0
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

a:link, a:visited {
  color: #D0D0D0;
  -webkit-transition: color 0.75s;
  -o-transition: color 0.75s;
  transition: color 0.75s
}

a:hover, a:active {
  color: goldenrod
}


